<template>
    <div class="login" align="center" style="margin: 10px">
        <h3>Iniciar sesión</h3>
        <v-form style="margin: 10px" align="center"><!--buscar utilidades v-form-->
        <v-text-field
                v-model="email"
                label="Email"
                :rules="[rules.required,rules.email]"
                counter
                outlined
                clearable
        ></v-text-field>

            <!--<p> <router-link  to = "/recuperar_correo">¿Has olvidado el correo electrónico?</router-link></p>-->
            <br>

        <v-text-field
                :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, rules.min]"
                :type="show3 ? 'text' : 'password'"
                label="Contraseña"
                class="input-group--focused"
                v-model="contrasena"
                counter
                outlined
                @click:append="show3 = !show3"
        ></v-text-field>

        </v-form>

        <p> <router-link  to = "/RecuperarContraseña">¿Has olvidado la contraseña?</router-link></p>
        <br>
        <v-btn @click="login" rounded color="primary" style="color: aqua; background: red" >Iniciar sesión</v-btn>

        <br>

        <h4>¿No tienes una cuenta? Puedes crearte una pulsando <router-link to = "/sign-up" >aquí</router-link></h4>
    </div>
</template>

<script>

    import  firebase from 'firebase'
    export default {
        name: "login",
        data(){
            return{
                email:'',
                contrasena:'',
                show3:false,
                rules: {
                    required: value => !!value || 'Campo requerido.',
                    min: v => v.length >= 8 || 'Mínimo 8 carácteres',
                    emailMatch: () => ('The email and password you entered don\'t match'),
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || 'Invalid e-mail.'
                    },
                },

            }
        },
        created:function() {
            this.email= '',
                this.contrasena= ''
        },
        methods:{
            login: function () {
                firebase.auth().signInWithEmailAndPassword(this.email,this.contrasena).then(
                    (user)=> {
                        alert('estas ahora logueado, '+this.email);
                        this.$router.replace('home')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );
            }
        }
    }
</script>

<style scoped>/*scoped limita el css to this component only*/


</style>