      <v-img src="https://firebasestorage.googleapis.com/v0/b/vue-firebase-tutorial-7ee46.appspot.com/o/FotosPerfil%2Ffoto?alt=media&token=7e0a9202-84bf-45b0-a795-efe8df2e10ae" aspect-ratio="1.7"></v-img>
