  </v-container>
</template>

<script>
  import * as firebase from 'firebase';
  import {mapActions} from 'vuex';
  import axios from 'axios';

  //import SingUp from './SingUp';

  //const { messaging } = firebase;

  export default {
  name: 'home',
  components: {
    //SingUp
  },


  data(){
    return{
      drawer: true,
      home:{title: 'Home', icon: 'mdi-home-city' , clickFuncion: 'funcionHome'},
      hogares:{title: 'Hogares', icon: 'mdi-home-city' , clickFuncion: 'funcionHogares'},
      dispositivos:{title: 'Dispositivos', icon: 'mdi-home-city' , clickFuncion: 'funcionDispositivos'},
      alertas:{title: 'Alertas', icon: 'mdi-home-city' , clickFuncion: 'funcionAlertas'},
      cerrarSesion:{title: 'Cerrar Sesión', icon: 'mdi-home-city' , clickFuncion: 'funcionCerrarSesion'},

      mini: true,

      usuario:
        {dato1: null,
        dato2: null,
        email: null,
        password : null,
        apellido: null,
        nombre:"null",
        telefono:null,
          foto:null,
        db: firebase.firestore()}
      ,
    }
  },
  created:function() {
    let db = firebase.firestore();
    let usuarioID = firebase.auth().currentUser.uid;
    let self = this
    console.log(usuarioID)
    db.collection("Usuarios").doc(usuarioID).onSnapshot(function (doc) {
      self.usuario.nombre=(doc.data().nombre)
      self.usuario.email=(doc.data().email)
      self.usuario.apellido=(doc.data().apellido)
      self.usuario.telefono=(doc.data().telefono)
      self.usuario.email=(doc.data().email)
      self.usuario.password=(doc.data().password)
      self.usuario.foto=(doc.data().foto)
    })
  },


  methods:{

    ...mapActions(
            ['guardarUsuario']
    ),
    logout: function () {
      firebase.auth().signOut().then(()=>{
        this.$router.replace('login')
      })
    },
    funcionHome:function(){
      console.log("hola caracola")
    },
    funcionHogares:function(){
      console.log("hola caracola")
      firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
              (user)=> {
                //alert('estas ahora logueado');
                this.$router.replace('Hogares')
                console.log(user)
              },
              function (err) {
                alert('Error: '+err.message)
                console.log('Opps '+err.message);
              }
      );

    },
    funcionDsipositivos:function(){
      console.log("voy a dispositivos")
      console.log("hola caracola")
      firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
              (user)=> {
                //alert('estas ahora logueado');
                this.$router.replace('Dispositivos')
                console.log(user)
              },
              function (err) {
                alert('Error: '+err.message)
                console.log('Opps '+err.message);
              }
      );
    },
    funcionAlertas:function(){
      console.log("hola caracola")
      this.guardarUsuario(this.usuario);
      let usuario2=this.$store.getters.getUsuario
      console.log("infoStore: "+usuario2.nombre);

    },
    funcionCerrarSesion:function(){
      firebase.auth().signOut().then(()=>{
        this.$router.replace('login')
      })
    },
    funcionEditarPerfil:function(){

      console.log("funcion ir perfil")
      firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
              (user)=> {
                //alert('estas ahora logueado');
                this.$router.replace('EditarPerfil')
                console.log(user)
              },
              function (err) {
                alert('Error: '+err.message)
                console.log('Opps '+err.message);
              }
      );

    },

      cargar : function(){
      firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
        dato1: this.usuario.dato1,
        dato2 : this.usuario.dato2,
      }, {merge:true}).then(function () {//con el merge, los nuevos datos que se añadan con nuesvos dato1, se sumaran a la laista, los que ya estan, se actualziaran
        //console.log("Documento escrito con id : ", firebase.auth().currentUser.uid);
      }).catch(function (error) {
        console.error("Error adding document: ", error);
      });
      let db=firebase.firestore();
        db.collection("Usuarios").get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            console.log(doc.data().imagen)
          });
        });

         db.collection("Usuarios").get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
              this.usuario.email=(doc.data().email)
          });
        });
         var storageRef = firebase.storage().ref();
         storageRef = storageRef.child('FotosPerfil/'+firebase.auth().currentUser.uid);
        var file = new File([this.usuario.foto], "foo.png",{
          type: "text/plain",
        });
         storageRef.put(file).then(function () {
           console.log('Uploaded a blob or file!');
         });

    },

    getMessagingToken () {
      firebase.messaging().getToken().then(async (token) => {
        if (token) {
          const currentMessageToken = window.localStorage.getItem('messagingToken')
          console.log('token will be updated', currentMessageToken != token)
          if(currentMessageToken != token){
            await this.saveToken(token)
          }
        } else {
          console.log('No Instance ID token available. Request permission to generate one.');
          this.notificationsPermisionRequest();
        }
      }).catch(function(err) {
        console.log('An error occurred while retrieving token. ', err);
      });
    },
    saveToken(token) {
      console.log('tokens', token)
      axios.post(`https://us-central1-vue-firebase-tutorial-7ee46.cloudfunctions.net/GeneralSubscription`,
              { token },
              {useCredentails:true})
              .then(response => {
                window.localStorage.setItem('messagingToken',token)
                console.log(response)
              }).catch((err) => {
        console.log(err)
      })
    },
    notificationsPermisionRequest () {
      firebase.messaging().requestPermission()
              .then(() => {
                this.getMessagingToken();
              })
              .catch((err) => {
                console.log('Unable to get permission to notify.', err);
              });
    },
    listenTokenRefresh() {
      const currentMessageToken = window.localStorage.getItem('messagingToken')
      console.log('currentMessageToken', currentMessageToken);
      if(currentMessageToken){
        firebase.messaging().onTokenRefresh(function() {
          firebase.messaging().getToken().then(function(token) {
            this.saveToken(token);
          }).catch(function(err) {
            console.log('Unable to retrieve refreshed token ', err);
          });
        });
      }
    },
  },
}
</script>
