<template>
    <div class="RecuperarContraseña">
        <h3>Recuperar contraseña</h3>
        <v-form style="width: 100% ;margin-right: 50px;margin-left: 50px" align="center"><!--buscar utilidades v-form-->
            <v-text-field
                    v-model="email"
                    label="Escribe el email destino de la nueva contraseña"
                    counter
                    outlined
                    clearable
            ></v-text-field>
        </v-form>

        <v-btn @click="recuperar" rounded color="primary" >Enviar</v-btn>
        <br><br>
        <h3>Volver a <router-link style="color: blue" to = "/login">Iniciar sesión</router-link>.</h3>

    </div>
</template>

<script>
    import  firebase from 'firebase'

    export default {
        name: "RecuperarContraseña",
        data(){
            return{
                email:'',
                contrasena:'',
                show3:false,
            }
        },
        methods:{



            recuperar: function () {
                var auth = firebase.auth();
                var emailAddress = this.email;
                auth.sendPasswordResetEmail(emailAddress).then(function() {
                    alert('Contraseña nueva enviada al correo');
                    this.$router.replace('login')

                }).catch(function(err) {
                    alert('Opps '+err.message)
                    console.log('Opps '+err.message);
                });
            }
        }
    }


</script>

