import firebase from "firebase";
import Vue from 'vue';
import Router from 'vue-router';

import Home from '@/views/Home.vue'
import Login from "@/views/Login.vue";
import EditarInformacion from "@/views/EditarInformacion";
import SignUp from "@/views/SingUp.vue";
import RecuperarContraseña from "@/views/RecuperarContraseña.vue";
import EditarPerfil from "@/views/EditarPerfil";
import Hogares from "@/views/Hogares.vue";
import Dispositivos from "@/views/Dispositivos.vue";

Vue.use(Router);

const router = new Router({
    routes:[
        {
            path:'*',
            redirect:'/login'
        },
        {
            path:'/',
            redirect:'/login',
        },
        {
            path:'/login',
            name: 'Login',
            component: Login
        },
        {
            path:'/sign-up',
            name: 'SignUp',
            component: SignUp
        },
        {
            path:'/RecuperarContraseña',
            name: 'RecuperarContraseña',
            component: RecuperarContraseña
        },
        {
            path:'/EditarPerfil',
            name: 'EditarPerfil',
            component: EditarPerfil,
            meta:{//condicion  que dice que requiere de autentificacion para entrar
                requiresAuth:true
            }
        },
        {
            path:'/Dispositivos',
            name: 'Dispositivos',
            component: Dispositivos,
            meta:{//condicion  que dice que requiere de autentificacion para entrar
                requiresAuth:true
            }
        },
        {
            path:'/EditarInformacion',
            name: 'EditarInformacion',
            component: EditarInformacion,
            meta:{//condicion  que dice que requiere de autentificacion para entrar
                requiresAuth:true
            }
        },
        {
            path:'/Hogares',
            name: 'Hogares',
            component: Hogares,
            meta:{//condicion  que dice que requiere de autentificacion para entrar
                requiresAuth:true
            }
        },
        {
            path: '/home',
            name: 'Home',
            component: Home,
            meta:{//condicion en la pantalla home, que dice que requiere de autentificacion para entrar
                requiresAuth:true
            }
        },
    ]
});



router.beforeEach((to, from, next)=>{//con esto miramos si los ususarios esta identificados o registrados, recorriendo los usuarios en firebase con el foreach
    //y lo sabemos gracias al requiereAuth de meta
    const currentUsers = firebase.auth().currentUser;
    const requiresAuth = to.matched.some(record=> record.meta.requiresAuth);

    if(requiresAuth && !currentUsers) next('login');//si requiere autentificacion y no esta en los usuarios
    else if(!requiresAuth && currentUsers) next('home');//si no requiere autentificacion y no esta en los usuarios
    else next();
});
export default router;