<template>
    <div class="Hogares">
        <v-row no-gutters justify="space-around">
            <v-card>
                <v-col style=" background: blue" >
                    <v-navigation-drawer  v-model="drawer" :mini-variant.sync="mini" permanent >
                        <v-list-item class="px-2">
                            <v-list-item-avatar>
                                <v-img src="../assets/descarga.png"></v-img>
                            </v-list-item-avatar>

                            <v-list-item-title @click="funcionEditarPerfil">{{this.usuario.nombre}}</v-list-item-title>

                            <v-btn icon @click.stop="mini = !mini">
                                <v-icon>mdi-chevron-left</v-icon>
                            </v-btn>
                        </v-list-item>

                        <v-divider></v-divider>

                        <!--mejor mostrar iconos y tal uno por uno y no con for, porque cada icono tendra su utilidad y la tendre que hacer especifica para cada uno-->

                        <v-list dense>
                            <v-list-item>
                                <v-list-item-icon>
                                    <v-icon>{{ home.icon }}</v-icon>
                                </v-list-item-icon>
                                <v-list-item-content >
                                    <v-list-item-title @click="funcionHome">{{ home.title }}</v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                                <v-list-item-icon>
                                    <v-icon>{{ hogares.icon }}</v-icon>
                                </v-list-item-icon>
                                <v-list-item-content >
                                    <v-list-item-title @click="funcionHogares">{{ hogares.title }}</v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                                <v-list-item-icon>
                                    <v-icon>{{ dispositivos.icon }}</v-icon>
                                </v-list-item-icon>
                                <v-list-item-content >
                                    <v-list-item-title @click="funcionDsipositivos">{{ dispositivos.title }}</v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                                <v-list-item-icon>
                                    <v-icon>{{ alertas.icon }}</v-icon>
                                </v-list-item-icon>
                                <v-list-item-content >
                                    <v-list-item-title @click="funcionAlertas">{{ alertas.title }}</v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                                <v-list-item-icon>
                                    <v-icon>{{ cerrarSesion.icon }}</v-icon>
                                </v-list-item-icon>
                                <v-list-item-content >
                                    <v-list-item-title @click="funcionCerrarSesion">{{ cerrarSesion.title }}</v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>
                        </v-list>

                    </v-navigation-drawer>

                </v-col>
            </v-card>

            <v-col>
                <div class="gb_Qe" style="visibility: hidden; left: 8px; top: 56px; display: none;"></div>
                <v-col cols="12" sm="12" md="6">
                    <v-card>
                        <v-toolbar>
                            <v-app-bar-nav-icon style="margin: auto">DISPOSITIVOS</v-app-bar-nav-icon>
                        </v-toolbar>


                        <v-container v-for="dispositivo in lista_dispositivos" :key="dispositivo.id">
                            <v-row dense>
                                <v-card color="#385F73" dark>
                                    <v-card-title class="headline">{{dispositivo.identificacion}}</v-card-title>
                                    <v-card-subtitle></v-card-subtitle>
                                    <v-card-actions>
                                        <v-btn  style="background: #42b983; margin: auto" text @click="botonDetalles()">Detalles</v-btn>
                                        <v-btn style="background: red ;margin: auto" text @click="botonBorrar(dispositivo)">Borrar</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-row>
                        </v-container>

                        <v-app style="height: 500px">
                            <v-dialog v-model="dialog" persistent  >
                                <template v-slot:activator="{ on }">
                                    <v-btn absolute fab right  color="primary" dark v-on="on">
                                        <v-icon >mdi-plus</v-icon>
                                    </v-btn>
                                </template>
                                <v-card>
                                    <v-card-title>
                                        <span class="headline">Nuevo Dispositivo</span>
                                    </v-card-title>
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12" sm="6" md="4">
                                                    <v-text-field v-model="dispositivo.identificacion" label="Numero de identificación"  ></v-text-field>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="funcionClose">Close</v-btn>
                                        <v-btn color="blue darken-1" text @click="funcionSave">Save</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </v-app>

                    </v-card>
                </v-col>
            </v-col>
        </v-row>


    </div>
</template>

<script>
    import * as firebase from 'firebase';
    //import SingUp from './SingUp';



    export default {
        name: 'Dispositivos',
        components: {
            //SingUp
        },
        dialog: false,


        data(){
            return{
                drawer: true,
                dialog: false,
                /*
                items: [
                  { title: 'Home', icon: 'mdi-home-city' , clickFuncion: 'funcionHome'},
                  { title: 'My Account', icon: 'mdi-account',clickFuncion: 'funcionAccound' },
                  { title: 'Users', icon: 'mdi-account-group-outline',clickFuncion: 'funcionUser' },
                  {title: 'Cerrar sesión', icon: 'cerrar-sesion',clickFuncion: 'funcionCerrar'}
                ],
                */
                home:{
                    title: 'Home',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionHome'
                },
                hogares:{
                    title: 'Hogares',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionHogares'
                },
                dispositivos:{
                    title: 'Dispositivos',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionDispositivos'
                },
                alertas:{
                    title: 'Alertas',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionAlertas'
                },
                cerrarSesion:{
                    title: 'Cerrar Sesión',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionCerrarSesion'
                },


                mini: true,

                dispositivo:{
                    identificacion:null,
                    usuarioAsociado: null,
                    db: firebase.firestore()
                },
                lista_dispositivos:[],

                usuario:
                    {dato1: null,
                        dato2: null,
                        email: null,
                        password : null,
                        apellido: null,
                        nombre:"null",
                        telefono:null,
                        foto:null,
                        db: firebase.firestore()}
                ,
            }

        },
        created:function() {
            let db = firebase.firestore();
            let self =this;
            db.collection("Usuarios").get().then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    this.usuario.nombre=(doc.data().nombre)
                    this.usuario.email=(doc.data().email)
                    this.usuario.apellido=(doc.data().apellido)
                    this.usuario.telefono=(doc.data().telefono)
                    this.usuario.email=(doc.data().email)
                    this.usuario.password=(doc.data().password)
                    this.usuario.foto=(doc.data().foto)
                    console.log(this.usuario.foto)//ahora al inicio, usuario esta cargado con null
                });
            });

            db.collection("Dispositivos").get().then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    // doc.data() is never undefined for query doc snapshots
                    let dispositivo={
                        id:doc.id,
                        ...doc.data()
                    }
                    self.lista_dispositivos.push(dispositivo)

                });
            });

        },


        methods:{


            logout: function () {
                firebase.auth().signOut().then(()=>{
                    this.$router.replace('login')
                })
            },
            funcionAnadir:function(){
                console.log("hola caracola")
            },
            funcionHome:function(){
                console.log("volver a home")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('home')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );
            },
            funcionHogares:function(){
                console.log("hola caracola")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('Hogares')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );

            },
            funcionDsipositivos:function(){
                console.log("hola caracola")
            },
            funcionAlertas:function(){
                console.log("hola caracola")
            },
            funcionCerrarSesion:function(){
                firebase.auth().signOut().then(()=>{
                    this.$router.replace('login')
                })
            },
            funcionEditarPerfil:function(){

                console.log("funcion ir perfil")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('EditarPerfil')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );

            },
            funcionClose: function(){
                console.log("close")
                this.dialog = false;

            },
            funcionSave: function(){
                console.log("save")
                this.dialog = false;
                firebase.firestore().collection("Dispositivos").doc(this.dispositivo.identificacion).set({
                    identificacion: this.dispositivo.identificacion,
                    usuarioAsociado: firebase.auth().currentUser.uid,
                },{merge:true});

            },
            botonDetalles: function () {
                console.log("pulsado boton detalles")
            },
            botonBorrar: function (dispositivo) {
                console.log("pulsado boton borrar el "+ dispositivo.identificacion)
                firebase.firestore().collection("Dispositivos").doc(dispositivo.identificacion).update({
                    identificacion:firebase.firestore.FieldValue.delete(),
                    usarioAsociado: firebase.firestore.FieldValue.delete(),
                    db:firebase.firestore.FieldValue.delete()
                }).then(function () {
                    console.log("Datos borrados");
                }).catch(function (error) {
                    console.error("Error removing datos: ", error);
                });
                //una vez borrado los campos, se borra ya el documento
                firebase.firestore().collection("Dispositivos").doc(dispositivo.identificacion).delete().then(function () {
                    console.log("Document successfully deleted!");
                }).catch(function (error) {
                    console.error("Error removing document: ", error);
                });

            }
        },




    }

</script>

<style scoped>

</style>