<template>
    <div class="EditarInformacion" style="margin: 10px">

        <v-row style="margin: 10px" >
            <v-list-item-avatar size="20%" @click="funcionHolaCaracola">
                <!--<v-img src="../assets/descarga.png"></v-img>-->
                <v-img ></v-img>
            </v-list-item-avatar>

            <v-file-input
                    style="margin: 10px; margin-right: 20%"
                    v-model="usuario.foto"
                    value=null
                    accept="image/png, image/jpeg, image/bmp"
                    label="Foto perfil"
                    prepend-icon="mdi-camera"
                    counter
                    outlined
                    clearable
            ></v-file-input>


        </v-row>
        <v-text-field
                v-model="usuario.nombre"
                label="Nombre"
                outlined
                shaped
        ></v-text-field>
        <v-text-field
                v-model="usuario.apellido"
                label="Apellidos"
                outlined
                shaped
        ></v-text-field>
        <v-text-field
                v-model="usuario.telefono"
                label="Telefono"
                outlined
                shaped
        ></v-text-field>
        <v-text-field
                :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show3 ? 'text' : 'password'"
                @click:append="show3 = !show3"
                v-model="usuario.password"
                class="input-group--focused"
                label="Conraseña"
                outlined
                shaped
        ></v-text-field>
        <v-row >
            <v-col>
                <v-btn @click="funcionCancelar" rounded color="primary" style="color: aqua; background: red" >Cancelar</v-btn>
            </v-col>
            <v-col>
                <v-btn @click="funcionGuardar" rounded color="primary" style="color: aqua; background: green" >Guardar</v-btn>

            </v-col>
        </v-row>



    </div>
</template>

<script>
    import * as firebase from 'firebase';
    export default {
        name: "EditarInformacion",
        components: {
            //SingUp
        },


        data(){
            return{
                nombre2: "algo",
                show3:false,
                usuario:
                    {
                        email: null,
                        password : null,
                        apellido: null,
                        nombre:"null",
                        telefono:null,
                        foto:null,
                        db: firebase.firestore()},
            }
        },
        label: 'algo!',
        created:function() {
            let db = firebase.firestore();
            db.collection("Usuarios").get().then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    this.usuario.nombre=(doc.data().nombre)
                    this.usuario.email=(doc.data().email)
                    this.usuario.apellido=(doc.data().apellido)
                    this.usuario.telefono=(doc.data().telefono)
                    this.usuario.email=(doc.data().email)
                    this.usuario.password=(doc.data().password)
                    //Intento ver si tiene fotod e perfil
                    var storageRef = firebase.storage().ref();
                    storageRef = storageRef.child(firebase.auth().currentUser.uid);
                    var file = new File([this.usuario.foto], "foo.png",{
                        type: "text/plain",
                    });
                    if(file.size > 9){
                        console.log("hay foto")
                        storageRef.put(file).then(function () {
                            console.log('Uploaded a blob or file!');
                        });
                        this.usuario.foto=file;
                    }else{
                        console.log("no hay foto")
                    }


                    console.log(this.usuario.foto)//ahora al inicio, usuario esta cargado con null
                });
            });
            var storage = firebase.storage();
            var gsReference = storage.refFromURL('gs://vue-firebase-tutorial-7ee46.appspot.com/'+firebase.auth().currentUser.uid);
            console.log("algo")
            console.log(gsReference)



        },
        methods:{
            funcionHolaCaracola(){
                console.log("hola caracola")
            },
            funcionGuardar : function(){
                firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
                    nombre: this.usuario.nombre,
                    apellido: this.usuario.apellido,
                    telefono: this.usuario.telefono,
                    password: this.usuario.password
                }, {merge:true}).then(function () {//con el merge, los nuevos datos que se añadan con nuesvos dato1, se sumaran a la laista, los que ya estan, se actualziaran
                    //console.log("Documento escrito con id : ", firebase.auth().currentUser.uid);
                }).catch(function (error) {
                    console.error("Error adding document: ", error);
                });

                var storageRef = firebase.storage().ref();
                storageRef = storageRef.child(firebase.auth().currentUser.uid);
                var file = new File([this.usuario.foto], "foo.png",{
                    type: "text/plain",
                });
                if(file.size > 9){
                    storageRef.put(file).then(function () {
                        console.log('Uploaded a blob or file!');
                    });
                }

            },
            funcionCancelar:function() {
                console.log("Volvemos a perfil")
                this.$router.replace('EditarPerfil')
            }
            ,
        },
    }
</script>





