<template>
    <div class="sign-up"  >
        <h3>Crear cuenta</h3>

        <div  style="margin-left: auto; margin-right: auto; width: 70%">
            <v-form >

                <v-text-field
                        v-model="nombre"
                        :rules="[rules.required]"
                        label="Nombre"
                        counter
                        outlined
                        clearable
                ></v-text-field>
                <v-text-field
                        v-model="apellido"
                        :rules="[rules.required]"
                        label="Apellidos"
                        counter
                        outlined
                        clearable
                ></v-text-field>
                <v-text-field
                        v-model="telefono"
                        :rules="[rules.required]"
                        label="Número de teléfono"
                        counter
                        outlined
                        clearable
                ></v-text-field>
                <v-file-input
                        v-model="foto"
                        value=null
                        accept="image/png, image/jpeg, image/bmp"
                        label="Foto perfil"
                        prepend-icon="mdi-camera"
                        counter
                        outlined
                        clearable
                ></v-file-input>
            </v-form>

        </div>


        <v-form style="margin-left: auto;margin-right: auto; width: 70%">

            <v-text-field
                    v-model="email"
                    label="Email"
                    :rules="[rules.required,rules.email]"
                    counter
                    outlined
                    clearable
            ></v-text-field>
            <v-text-field
                    :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required, rules.min]"
                    :type="show3 ? 'text' : 'password'"
                    label="Contraseña"
                    class="input-group--focused"
                    v-model="password"
                    counter
                    outlined
                    @click:append="show3 = !show3"
            ></v-text-field>


            <button style="color: blue" @click="singUp">Registrarse</button>
            <span> o volver a <router-link style="color: blue" to = "/login">Iniciar sesión</router-link>.</span>

        </v-form>


    </div>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "SingUp",
        data(){//son los dos datos que se manejan, email y pasword
            return{
                email: '',
                foto:null,//si foto esta null, no se ha subido imagen y se pondra otra pord efecto
                password:'',
                apellido:'',
                nombre:'',
                telefono:'',
                show3:false,
                rules: {
                    required: value => !!value || 'Campo requerido.',
                    min: v => v.length >= 8 || 'Mínimo 8 carácteres',
                    emailMatch: () => ('The email and password you entered don\'t match'),
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || 'Invalid e-mail.'
                    },
                },
            }
        },

        methods:{
            singUp: function () {
                firebase.auth().createUserWithEmailAndPassword(this.email,this.password).then(
                     (user)=> {
                         //intento guardar los datos del usuario en firestone
                         firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
                             email: this.email,
                             password : this.password,
                             apellido: this.apellido,
                             nombre:this.nombre,
                             telefono:this.telefono,
                             foto:null
                         }, {merge:true}).then(function () {//con el merge, los nuevos datos que se añadan con nuesvos dato1, se sumaran a la laista, los que ya estan, se actualziaran
                             console.log("Documento escrito con id : ", firebase.auth().currentUser.uid);
                         }).catch(function (error) {
                             console.error("Error adding document: ", error);
                         });
                         let db=firebase.firestore();
                         db.collection("Usuarios").get().then((querySnapshot) => {
                             querySnapshot.forEach((doc) => {
                                 console.log(doc.data().email)
                             });
                         });
                         //intento guardar la foto
                         /*
                         var storageRef = firebase.storage().ref();
                         var FotosPerfil = storageRef.child('FotosPerfil');
                         var imageFotosPerfil = storageRef.child('FotosPerfil/foto.jpg');
                         console.log(imageFotosPerfil)
                         console.log(FotosPerfil)
                         var file = File(this.foto);
                         FotosPerfil.put(file).then(function () {
                             console.log('Uploaded a blob or file!');
                         })
                         */
                        alert('estas ahora resgitrado');
                        this.$router.replace('home')
                         console.log(user)
                         //hacer una tabla con los datos
                    },
                    function (err) {
                        alert('Opps '+err.message)
                    }
                );
            }
        }
    }
</script>

