db.enablePersistence({experimentalTabSynchronization:true})
const storage = firebase.storage()
const messaging = firebase.messaging()

export default {
  db,
  storage,
  messaging
}

firebase.auth().onAuthStateChanged(()=>{//aqui iniciomos la app soloc uando el firebase auth este lsito para usarse
  if(!app){
    app= new Vue({
      router,
      store,
      db,
      storage,
      vuetify,
      render:h=>h(App)
    }).$mount('#app');
  }
})




