<template>
    <div class="EditarPerfil" >

        <v-row justify="space-around">
            <v-card>
            <v-col style="background: #7277ff" align="left" sm="6">
                <v-navigation-drawer v-model="drawer" :mini-variant.sync="mini" permanent >
                    <v-list-item class="px-2">
                        <v-list-item-avatar>
                            <v-img src="../assets/descarga.png"></v-img>
                        </v-list-item-avatar>

                        <v-list-item-title @click="funcionEditarPerfil">{{this.usuario.nombre}}</v-list-item-title>

                        <v-btn icon @click.stop="mini = !mini">
                            <v-icon>mdi-chevron-left</v-icon>
                        </v-btn>
                    </v-list-item>

                    <v-divider></v-divider>

                    <!--mejor mostrar iconos y tal uno por uno y no con for, porque cada icono tendra su utilidad y la tendre que hacer especifica para cada uno-->

                    <v-list dense>
                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>{{ home.icon }}</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content >
                                <v-list-item-title @click="funcionHome">{{ home.title }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>{{ hogares.icon }}</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content >
                                <v-list-item-title @click="funcionHome">{{ hogares.title }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>{{ dispositivos.icon }}</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content >
                                <v-list-item-title @click="funcionHome">{{ dispositivos.title }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>{{ alertas.icon }}</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content >
                                <v-list-item-title @click="funcionHome">{{ alertas.title }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>{{ cerrarSesion.icon }}</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content >
                                <v-list-item-title @click="funcionCerrarSesion">{{ cerrarSesion.title }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list>

                </v-navigation-drawer>

            </v-col>
            </v-card>
                <v-col style="align: center; width: auto" >
                    <div>
                        <v-form style="width: 10%; margin: auto" align="center">
                            <v-img src="../assets/descarga.png"></v-img>
                        </v-form>
                        <h3 >Información personal</h3>
                        <div >
                            Nombre: {{usuario.nombre}}<br>
                            Apellido: {{usuario.apellido}}<br>
                            Correo electrónico: {{usuario.email}}<br>
                            Telefono:  {{usuario.telefono}}
                        </div>
                    </div>
                    <v-btn  @click="funcionCambiarDatos" rounded color="primary" style="color:black ; background: beige; margin: 10px; width: auto" >Editar información</v-btn>
                </v-col>

        </v-row>





    </div>
    
</template>

<script>
    import * as firebase from 'firebase';
    export default {
        name: "EditarPerfil",
        components: {
            //SingUp
        },
        data(){
            return{
                drawer: true,
                home:{
                    title: 'Home',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionHome'
                },
                hogares:{
                    title: 'Hogares',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionHogares'
                },
                dispositivos:{
                    title: 'Dispositivos',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionDispositivos'
                },
                alertas:{
                    title: 'Alertas',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionAlertas'
                },
                cerrarSesion:{
                    title: 'Cerrar Sesión',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionCerrarSesion'
                },


                mini: true,
                usuario:
                    {
                        email: null,
                        password : null,
                        apellido: null,
                        nombre:"null",
                        telefono:null,
                        foto:null,
                        db: firebase.firestore()},
            }
        },
        created:function() {
            let db = firebase.firestore();
            db.collection("Usuarios").get().then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    this.usuario.nombre=(doc.data().nombre)
                    this.usuario.email=(doc.data().email)
                    this.usuario.apellido=(doc.data().apellido)
                    this.usuario.telefono=(doc.data().telefono)
                    this.usuario.email=(doc.data().email)
                    this.usuario.password=(doc.data().password)
                    this.usuario.foto=(doc.data().foto)
                    console.log(this.usuario.foto)//ahora al inicio, usuario esta cargado con null
                });
            });
        },
        methods:{
            logout: function () {
                firebase.auth().signOut().then(()=>{
                    this.$router.replace('login')
                })
            },
            funcionHome:function(){
                console.log("funcion volver home")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('home')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );
            },
            funcionHogares:function(){
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('Hogares')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );
            },
            funcionDsipositivos:function(){
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('Dispositivos')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );
            },
            funcionAlertas:function(){
                console.log("hola caracola")
            },
            funcionCerrarSesion:function(){
                firebase.auth().signOut().then(()=>{
                    this.$router.replace('login')
                })
            },
            funcionCambiarDatos: function(){
                console.log("funcion ir editar informacion")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        //alert('estas ahora logueado');
                        this.$router.replace('EditarInformacion')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );
            },
            funcionEditarPerfil:function(){
                firebase.auth().signOut().then(()=>{
                    this.$router.replace('EditarPerfil')
                })
            },

            cargar : function(){
                firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
                    dato1: this.usuario.dato1,
                    dato2 : this.usuario.dato2,
                }, {merge:true}).then(function () {//con el merge, los nuevos datos que se añadan con nuesvos dato1, se sumaran a la laista, los que ya estan, se actualziaran
                    //console.log("Documento escrito con id : ", firebase.auth().currentUser.uid);
                }).catch(function (error) {
                    console.error("Error adding document: ", error);
                });
                let db=firebase.firestore();
                db.collection("Usuarios").get().then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        console.log(doc.data().imagen)
                    });
                });

                db.collection("Usuarios").get().then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        this.usuario.email=(doc.data().email)
                    });
                });


                var storageRef = firebase.storage().ref();
                storageRef = storageRef.child('FotosPerfil/'+firebase.auth().currentUser.uid);

                var file = new File([this.usuario.foto], "foo.png",{
                    type: "text/plain",
                });
                storageRef.put(file).then(function () {
                    console.log('Uploaded a blob or file!');
                });

            }
        },
    }

</script>

<style scoped>

</style>